import React from 'react';
import { string } from 'prop-types';
import { Row } from 'simple-flexbox';
import { StyleSheet, css } from 'aphrodite';

const styles = StyleSheet.create({

    container: {
        height: 40
    },

    cursorPointer: {
        cursor: 'pointer'
    },

    name: {
        fontFamily: 'Muli',
        fontStyle: 'normal',
        fontWeight: 600,
        fontSize: 14,
        lineHeight: '20px',
        textAlign: 'right',
        letterSpacing: 0.2,
        '@media (max-width: 768px)': {
            display: 'none'
        }
    },

    title: {
        fontFamily: 'Muli',
        fontStyle: 'normal',
        fontWeight: 'bold',
        fontSize: 24,
        lineHeight: '30px',
        letterSpacing: 0.3,
        '@media (max-width: 768px)': {
            marginLeft: 36
        },
        '@media (max-width: 468px)': {
            fontSize: 20
        }
    },

    iconStyles: {
        cursor: 'pointer',
        marginLeft: 25,
        '@media (max-width: 768px)': {
            marginLeft: 12
        }
    }

});

function HeaderComponent(props) {

    const { icon, title, ...otherProps } = props;

    return (

        <Row
            className={css(styles.container)}
            vertical="center"
            horizontal="space-between"
            {...otherProps}
        >

            <span className={css(styles.title)}>
                {title}
            </span>

            <Row vertical="center">

                <Row vertical="center">

                    <span className={css(styles.name, styles.cursorPointer)}>
                        'Nombre de Usuario'
                    </span>

                </Row>

            </Row>

        </Row>

    );

}

HeaderComponent.propTypes = {
    title: string
};

export default HeaderComponent;